// ** React Imports
import { Link } from 'react-router-dom'

// ** Icons Imports
import { MoreVertical, Trash2, Archive } from 'react-feather'

// ** Reactstrap Imports
import { Badge, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap'
import { statusText } from '../../functions/customfunctions'
import { handleRemoveEmployee } from './delete'

// ** Renders Status Columns

const statusObj = {
  active: 'light-success',
  inactive: 'light-secondary'
}

export const columns = [
  {
    name: 'Name',
    sortable: true,
    minWidth: '200px',
    sortField: 'Name',
    selector: row => row.name,
    cell: row => (
      <div className='d-flex justify-content-left align-items-center'>
        {/* {renderClient(row)} */}
        <div className='d-flex flex-column'>
          <Link
            to={`/edit-product/${row._id}`}
            className='user_name text-truncate text-body'
            
          >
            <span className='fw-bolder'>{row.name}</span>
          </Link>
        </div>
      </div>
    )
  },
  {
    name: 'Color',
    minWidth: '150px',
    sortable: true,
    sortField: 'color',
    selector: row => row.color,
    cell: row => {
      const badgeElements = row.color.map(color => (
        <Badge key={color} className={`text-capitalize`} color={"light-secondary"} pill>
          {color}
        </Badge>
      ))
      // Return the array of Badge elements wrapped in a div (or another container)
      return (
        <div>
          {badgeElements}
        </div>
      )
    }
  },
  {
    name: 'Price',
    minWidth: '130px',
    sortable: true,
    sortField: 'price',
    selector: row => row.price,
    cell: row => <span className='text-capitalize'>{row.price}</span>
  },
  {
    name: 'Quantity',
    minWidth: '150px',
    sortable: true,
    sortField: 'quantity',
    selector: row => row.quantity,
    cell: row => <span className='text-capitalize'>{row.quantity}</span>
  },
  {
    name: 'Status',
    minWidth: '138px',
    sortable: true,
    sortField: 'status',
    selector: row => row.status,
    cell: row => (
      <Badge className='text-capitalize' color={statusObj[statusText(row.status)]} pill>
        {statusText(row.status)}
      </Badge>
    )
  },
  {
    name: 'Description',
    minWidth: '250px',
    sortable: true,
    sortField: 'description',
    selector: row => row.description,
    cell: row => <span className='text-capitalize'>{row.description}</span>
  },
  {
    name: 'Actions',
    minWidth: '150px',
    cell: row => (
      <div className='column-action'>
        <UncontrolledDropdown>
          <DropdownToggle tag='div' className='btn btn-sm'>
            <MoreVertical size={14} className='cursor-pointer' />
          </DropdownToggle>
          <DropdownMenu>
            <DropdownItem 
              tag={Link}
              className='w-100'
              to={`/edit-product/${row._id}`}
            >
              <Archive size={14} className='me-50' />
              <span className='align-middle'>Edit</span>
            </DropdownItem>
            <DropdownItem
              tag='a'
              href='/'
              className='w-100'
              onClick={e => {
                e.preventDefault()
                handleRemoveEmployee(row._id)
                // store.dispatch(deleteUser(row._id))
              }}
            >
              <Trash2 size={14} className='me-50' />
              <span className='align-middle'>Delete</span>
            </DropdownItem>
          </DropdownMenu>
        </UncontrolledDropdown>
      </div>
    )
  }
]
